<template>
  <div>
    <div class="under-cell-wapper">
      <div class="right-abslute-up">
          <span class="fix-text">0.</span>
          <form @submit="boardSearchClick" action="#">
            <input @textInput="textInutCheck" @focus="focusHandle" @blur="blurHandle" class="rate-text" type="number" ref="input" v-model="currentValue" @click="textClickHandle" :placeholder="cellData.salescommission" >
          </form>
      </div>
      <div class="right-abslute-down">
        <span class="icon-jiantou-copy iconfont" @click="lookShopsHandle"></span>
      </div>
      <div class="cell-info">
        <div class="cell-info-left">
          <div class="img-container">
            <img v-lazy="cellData.headimgurl" class="img" />
          </div>
        </div>
        <div class="cell-info-right">
          <p class="name">{{cellData.username}}</p>
          <p class="icon-phone iconfont"><span class="under-phone">{{telephone}}</span></p>
          <p class="relieve" @click="relieveCall">解除{{job}}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import { mapState } from "vuex";
import { Toast, Button } from "mint-ui";
import "mint-ui/lib/button/style.css";
import "mint-ui/lib/toast/style.css";
Vue.component(Button.name, Button);
Vue.component(Toast.name, Toast);

export default {
  name: "agency-under-cell",
  data() {
    return {
      currentValue: 0
    };
  },
  props: {
    cellData: {
      type: Object,
      default: function() {
        return {
          headimgurl: "http://www.jubenxiong.com/aml/img/nx_1515661463680.png",
          phone: "未绑定",
          username: "L先生",
          role: 1,
          userid: "",
          salescommission: 0
        };
      }
    },
    isReplace:false
  },
  components: {},
  activated() {},
  mounted() {
    this.cellData.salescommission = this.clearTailZero(this.cellData.salescommission*100);
    this.currentValue = this.cellData.salescommission;
  },
  methods: {
    relieveCall(){
      if(this.$parent.$parent.relieveCall){
        this.$parent.$parent.relieveCall(this.cellData);
      }else this.$parent.$parent.$parent.relieveCall(this.cellData);
    },
    lookShopsHandle() {
      let titleName = this.cellData.username + " - 客户列表";
      if(this.isReplace) this.$router.replace({name: "userlist",params: { titleName: this.titleName, userid: this.cellData.userid }});
      else this.$router.push({name: "userlist",params: { titleName: this.titleName, userid: this.cellData.userid }});
    },
    clearTailZero(value){     
      var tempNum=parseInt(value)/100;
      if(tempNum==0) return "0";
      return tempNum.toString().substr(2);
    },
    textInutCheck(event) {
      if(!/^[0-9]*$/.test(event.data)){
        event.returnValue=false;
        return;
      }
      if(this.currentValue.length==0){
        if(parseInt(event.data)>5){
          Toast({message: "分成比例不能大于0.5",duration: 1500});
          event.returnValue=false;
        } 
      }else if(this.currentValue.length==1){
        if(parseInt(this.currentValue+event.data)>50){
          Toast({message: "分成比例不能大于0.5",duration: 1500});
          event.returnValue=false;
        }
      }else{
        event.returnValue=false;
      }
    },
    focusHandle(event) {
      this.currentValue="";
    },
    boardSearchClick(boardEvent){
      if(boardEvent) boardEvent.preventDefault();
      this.$refs.input.blur();
    },
    blurHandle(event){
      if(this.currentValue==""){
        this.restoreRate();
        return;
      }
      if(this.currentValue.toString().length==1) this.currentValue+="0";
      this.currentValue=this.clearTailZero(this.currentValue);
      if(this.currentValue.toString()==this.cellData.salescommission.toString()){
        this.restoreRate();
        return;
      }
      this.$store.dispatch("toggleLoading", true);
      this.$http
      .post(this.path + "php/Route.php", {
        type:'updatecommission',
        aid:this.userid,
        uid:this.cellData.userid,
        scom:"0."+this.currentValue
      })
      .then(response => {
        let irv = response.body.error;
        if(irv=="ok"){
          Toast({message: "成功修改分成比例!",iconClass: "iconfont icon-tishi1",duration: 2000});
          this.cellData.salescommission=this.currentValue;
        }else{
          Toast({message: "分成比例修改失败!",iconClass: "iconfont icon-tishi1",duration: 2000});
          this.restoreRate;
        }
        this.$store.dispatch("toggleLoading", false);
      })
      .catch(response=>{
        //请求出错处理
        console.log(response);
        this.$store.dispatch("toggleLoading", false);
      });
    },
    restoreRate(){
      this.currentValue = this.cellData.salescommission;
    },
    textClickHandle(event) {
      event.stopPropagation();
    }
  },
  computed: {
    ...mapState(["openid", "path","userid"]),
    telephone: function() {
      console.log("this.phone=" + this.phone);
      if (this.cellData.phone && this.cellData.phone != "") {
        return this.cellData.phone;
      } else return "未绑定";
    },
    job(){
      return this.cellData.role.toString()=="3"?"业务员":"美导";
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.under-cell-wapper {
  width: 100%;
  position: relative;
  height: 78px;

  .cell-info {
    height: 100%;
    font-size: 0px;
    // background-color: #fff;
    display: flex;
    align-items: center;

    .cell-info-left {
      flex-grow: 0;
      flex-shrink: 1;
      flex-basis: 80px;
      text-align: center;

      .img-container {
        display: inline-block;
        width: 60px;
        height: 60px;

        .img {
          width: 100%;
          height: 100%;
          border-radius: 50%;
          position: relative;
        }
      }
    }

    .cell-info-right {
      flex-grow: 1;
      flex-shrink: 1;
      flex-basis: 0;

      .name {
        max-width: 120px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        font-size: 15px;
        font-weight: 700;
        color: #42495C;
        padding: 3px 0px 0px 0px;
      }

      .icon-phone {
        position: relative;
        left: -2px;
        font-size: 14px;
        color: #878a94;
        padding: 3px 0px 5px 0px;

        .under-phone {
          color: #aaa;
          font-size: 13px;
        }
      }

      .relieve {
        display: inline-block;
        font-size: 13px;
        color: #f3f3f3;
        padding: 4px 8px 3px 8px;
        border-radius: 4px;
        background-color: #999;
      }
    }
  }
  .right-abslute-down{
    position: absolute;
    right: 12px;
    bottom:12px;
    z-index: 5;
    .icon-jiantou-copy{
      color #9a9a9a;
      font-size 18px;
      padding 6px;
    }
  }

  .right-abslute-up {
    position: absolute;
    right: 8px;
    top: 38%;
    transform: translate(0, -50%);
    z-index: 6;

    .fix-text{
      color: #42495C;
      font-size: 14px;
      position relative;
      left 24px;
      pointer-events:none;
    }

    form {
      display: inline;

      .rate-text {
        padding-left 22px;
        -webkit-appearance: none; // 去掉iphone背景阴影
        border: none;
        outline: none;
        max-width 26px;
        border-radius: 4px;
        -webkit-touch-callout: none;
        text-align: left;
        color: #42495C;
        font-size: 14px;
      }

      .rate-text::-webkit-input-placeholder {
        color: #acacac;
        font-size: 14px;
      }

      .rate-text::-webkit-search-cancel-button {
        -webkit-appearance: none; // 此处只是去掉默认的小×
      }

      .rate-text:focus {
        transition: border linear 0.2s, box-shadow linear 0.2s;
        -webkit-transition: border linear 0.2s, -webkit-box-shadow linear 0.1s;
        box-shadow: 0 0 3px rgba(106, 106, 106, 0.105); // 向外漫射程度1
        -webkit-box-shadow: 0 0 3px rgba(106, 106, 106, 1.5); // 向外漫射程度3
      }
    }
  }
}
</style>



