<template>
    <div class="ac-under-search">
        <div v-show="state=='0'" class="history">
            <div class="head">
              <span class="tip-word">最近搜索</span>
              <span class="icon-lajilou iconfont" @click="clearHistory"></span>
            </div>
            <div class="history-item-container">
              <div v-for="searchWord in historyData" class="history-item" @click="searchHistory(searchWord)" >{{searchWord}}</div>
            </div>
        </div>
        <div v-show="state=='1'" class="search-result">
          <div v-show="showEmpty==false" @touchstart="scrollFixStart" @touchmove="scrollFixMove" class="under-cell-wapper">
            <div class="result-words">搜索结果如下：</div>
            <agency-under-cell v-for="cellData in searchData" :is-replace="true" :cell-data="cellData" class="agency-under-cell"></agency-under-cell>
          </div>
          <div v-show="showEmpty==true" class="empty-container">
                <empty-bg :msg="emptyMsg"></empty-bg>
          </div>
        </div>
    </div>
</template>

<script>
import { mapState } from "vuex";
import { savaToLocal, loadFromlLocal } from "../../../../common/js/storage.js"; // 公共方法：本地缓存
import bus from "../../../../common/js/eventBus.js";
import EmptyBg from "../../../popupBox/emptyBg.vue";
import agencyUnderCell from "../../../public/agencyUnderCell.vue";

export default {
  name: "ac-under-search",
  data() {
    return {
      historyData: null,
      searchData: [], //搜索到了下限列表数据
      curSearchKey: "", //当前正在搜索的关键字
      state: "0", //0代表显示历史状态，1代表搜索结果状态
      emptyMsg: "未找到相关结果~",
      showEmpty: false
    };
  },
  components: {
    EmptyBg,
    agencyUnderCell
  },
  activated() {
    this.historyData = loadFromlLocal("aml", "ac-under-search", []);
    this.$store.dispatch("toggleLoading", false);
  },
  mounted() {
    bus.$on("cancel-refresh-upper-data", () => {
      if(this.searchData.length==0) return;
      this.searchData=[];
      this.showEmpty = true;
    });
    bus.$on("search-Handle", searchKey => {
      if (this.$route.name != "agencyChainSearch") return;
      console.log(
        "agencyChainSearch收到了来自mySearch的search-Handle事件,搜索关键字为:" +
          searchKey
      );
      if (this.curSearchKey == searchKey) return;
      this.curSearchKey = searchKey;
      this.searchUnderHandle(searchKey);
    });
    bus.$on("searchtext-empty", () => {
      if (this.$route.name != "agencyChainSearch") return;
      console.log("agencyChainSearch收到了来自mySearch的searchtext-empty事件");
      this.backToHistory();
    });
  },
  beforeRouteLeave(to, from, next) {
    //发送搜索界面离开事件，让搜索导航复位
    this.$emit("search-panel-leave");
    this.curSearchKey = "";
    this.searchData = [];
    this.historyData = null;
    this.showEmpty = false;
    this.state = "0";
    next();
  },
  computed: {
    ...mapState(["openid", "path","userid","role"])
  },
  methods: {
    scrollFixStart(e) {
      var el = e.currentTarget;
      if (el.scrollTop === 0) {
        el.scrollTop = 1;
      } else if (el.scrollHeight == el.scrollTop + el.offsetHeight) {
        el.scrollTop = el.scrollTop - 1;
      }
    },
    scrollFixMove(e) {
      var el = e.currentTarget;
      if (el.scrollHeight > el.clientHeight) {
        e.stopPropagation();
      }
    },
    clearHistory() {
      //清空历史记录
      this.historyData = [];
      savaToLocal("aml", "ac-under-search", this.historyData);
    },
    searchHistory(searchWord) {
      bus.$emit("update-search-text", searchWord);
      this.searchUnderHandle(searchWord);
    },
    addHistory(historyWord) {
      //添加历史记录
      this.historyData.unshift(historyWord);
      this.saveHistory();
    },
    filterRepeatHistory() {
      //去掉历史记录里重复的搜索关键字
      this.historyData = this.historyData.filter(function(
        element,
        index,
        self
      ) {
        return self.indexOf(element) == index;
      });
    },
    saveHistory() {
      //保存历史记录
      this.filterRepeatHistory();
      if (this.historyData.length > 10) this.historyData.pop();
      savaToLocal("aml", "ac-under-search", this.historyData);
    },
    backToHistory() {
      //返回至历史界面
      if (this.state == "0") return;
      this.showEmpty = false;
      this.state = "0";
      this.searchData=[];
    },
    searchUnderHandle(searchKey) {
      //开始搜索下限
      this.addHistory(searchKey);
      this.state = "1";
      this.$store.dispatch("toggleLoading", true);
      this.$http
        .post(this.path + "php/Route.php", {
          type: "afubyphone",
          aid: this.userid,
          phone: searchKey,
          role: 0
        })
        .then(response => {
          let resultData = response.body.arr;
          if (!resultData || resultData.length == 0) this.showEmpty = true;
          else{
            let personData=resultData[0];
            if(personData.role.toString()!="4"&&personData.role.toString()!="3") this.showEmpty = true;
            else{
              this.showEmpty = false;
              this.searchData = resultData;
            } 
          } 
          this.$store.dispatch("toggleLoading", false);
        })
        .catch(response=>{
          //请求出错处理
          console.log(response);
          this.$store.dispatch("toggleLoading", false);
        });
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.ac-under-search {
  width: 100%;
  flex: 1;
  display: flex;
  flex-flow: column nowrap;
  background-color: #ffffff;

  .search-result {
    width: 100%;
    flex: 1;
    position: relative;

    .under-cell-wapper {
      height: 100%;
      width: 100%;
      padding: 0px 8px;
      position: absolute;
      overflow: auto;
      -webkit-overflow-scrolling: touch;
      box-sizing: border-box;

      .result-words {
        width: 100%;
        margin-top: 10px;
        color: #333;
        font-size: 14px;
        line-height: 30px;
        border-bottom: 1px dashed #dedede;
      }

      .agency-under-cell {
        width: 100%;
        border-bottom: 1px solid #ddd;
      }
    }

    .empty-container {
      height: 100%;
      width: 100%;
      position: absolute;
    }
  }

  .history {
    position: absolute;
    left: 13px;
    right: 13px;

    .head {
      width: 100%;
      display: table;
      height: 38px;
      border-bottom: 1px dashed #dedede;

      .tip-word {
        width: 50%;
        display: table-cell;
        color: #333;
        font-size: 14px;
        line-height: 38px;
        text-align: left;
        padding-left: 2px;
      }

      .icon-lajilou {
        width: 50%;
        display: table-cell;
        color: #7a7a7a;
        font-size: 24px;
        line-height: 38px;
        text-align: right;
        padding-right: 7px;
        position: relative;
        top: 5px;
      }
    }

    .history-item-container {
      margin-top: 12px;

      .history-item {
        display: inline-block;
        background: #e3e3e3;
        color: #515151;
        font-size: 14px;
        line-height: 14px;
        padding: 6px 6px 4px 6px;
        border-radius: 4px;
        margin-bottom: 12px;
        margin-right: 10px;
      }
    }
  }
}
</style>



